import {
  ActionGroup,
  darkTheme,
  Flex,
  Item,
  lightTheme,
  Provider,
  Text,
  View,
} from "@adobe/react-spectrum";
import { ToastContainer } from "@react-spectrum/toast";
import { createElement, Key, useEffect, useState } from "react";
import {
  ModuleType,
  RegistrationModule,
  RenameModule,
  SignAgentModule,
} from "./modules/index";
import { subscribeBackgroundColor } from "../lib/utils/bolt";

const modules = [RegistrationModule, RenameModule, SignAgentModule];

const Modules = () => {
  const [selectedTab, setSelectedTab] = useState<string>("reg");

  const handleTabChange = (key: Key) => {
    setSelectedTab(key.toString());
  };

  return (
    <>
      <ActionGroup
        items={modules}
        selectionMode="single"
        onAction={handleTabChange}
        defaultSelectedKeys={["reg"]}
        selectedKeys={[selectedTab]}
        buttonLabelBehavior="hide"
      >
        {(item: ModuleType) => (
          <Item key={item.key}>
            {item.icon}
            <Text>{item.name}</Text>
          </Item>
        )}
      </ActionGroup>
      <View>
        {modules.map((item: ModuleType) => (
          <View key={item.key} isHidden={selectedTab !== item.key}>
            {createElement(item.component)}
          </View>
        ))}
      </View>
    </>
  );
};

const Main = () => {
  const [appBackgroundColor, setAppBackgroundColor] = useState("");
  const [appTheme, setAppTheme] = useState(darkTheme);
  const [colorScheme, setColorScheme] = useState<"dark" | "light">("dark");

  useEffect(() => {
    subscribeBackgroundColor((color) => {
      const [r, g, b] = color.match(/\d+/g)?.map(Number) || [0, 0, 0];
      console.log(r, g, b);
      switch (true) {
        case r === 50 && g === 50 && b === 50:
          setAppTheme(darkTheme);
          setColorScheme("dark");
          setAppBackgroundColor("#1d1d1d");
          break;
        case r === 83 && g === 83 && b === 83:
          setAppTheme(darkTheme);
          setColorScheme("light");
          setAppBackgroundColor("#323232");
          break;
        case r === 184 && g === 184 && b === 184:
          setAppTheme(lightTheme);
          setColorScheme("light");
          setAppBackgroundColor("#fdfdfd");
          break;
        case r === 240 && g === 240 && b === 240:
          setAppTheme(lightTheme);
          setColorScheme("light");
          setAppBackgroundColor("#fdfdfd");
          break;
        default:
          setAppTheme(darkTheme);
          setColorScheme("dark");
      }
    });
  }, []);

  return (
    <Provider theme={appTheme} colorScheme={colorScheme} scale="medium">
      <style>{`
        html, body, #root{
          margin: 0;
          padding: 0;
          height: 100%;
          overflow: auto;
          background-color:  ${appBackgroundColor};
        }
      `}</style>

      <View
        padding={"calc(single-line-height / 2"}
        paddingTop={"calc(single-line-height / 2.5"}
      >
        <Flex
          direction={"column"}
          maxWidth={"static-size-5000"}
          margin={"auto"}
        >
          <Modules />
        </Flex>
        <ToastContainer />
      </View>
    </Provider>
  );
};
export default Main;
